<script>
  import { quadOut as easing } from 'svelte/easing'
  import Scrollmation, {
    toHomeRatio,
    toStartRatio,
    toEndRatio,
    toRangeRatio,
    fullRangePx,
    toHomePx,
    toEndPx,
    toStartPx
  } from "scrollmation";

  let x = 0;
  let c = 0;
  let scrollToPosition = null;
  // let dir = 1

  // let interval = 1;
  // let i = setInterval(() => {
  //   c += 0.12;
  // }, interval);

  function onScroll(evt) {
    c = toRangeRatio(evt.detail)*440;
    // console.log({
    //   // toHomeRatio: toHomeRatio(evt.detail),
    //   // toStartRatio: toStartRatio(evt.detail),
    //   // toEndRatio: toEndRatio(evt.detail),
    //   // toRangeRatio: toRangeRatio(evt.detail),
    //   // fullRangePx: fullRangePx(evt.detail),
    //   // toHomePx: toHomePx(evt.detail),
    //   // toEndPx: toEndPx(evt.detail),
    //   // toStartPx: toStartPx(evt.detail)
    // });
  }

  function scrollTo(pos) {
    scrollToPosition = pos;
  }

  function onClickPrev() {
    scrollTo("start");
  }

  function onClickNext() {
    scrollTo("end");
  }

  function onKeyDown({ key }) {
    if (key === "ArrowRight" ) {
      scrollTo("start");
    }
    if (key === "ArrowUp" || key === "ArrowDown") {
      scrollTo("home");
    }
    if (key === "ArrowLeft") {
      scrollTo("end");
    }
  }

  $: x = c-20 ;
  $: y = Math.cos(x / 60);
</script>

<style>
  .sincontainer {
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    position: absolute;
    z-index: -1;
    padding: 20px;
  }

  .sindot {
    position: absolute;
    border-radius: 10px;
    fill: #f00;
    opacity: 0.5;
    stroke-width:0;
    
  }
  .maindot{

    r: 10;
  }
</style>

<svelte:window on:keydown={onKeyDown} />
  <!-- <a href="#" on:click|preventDefault={onClickPrev}>prev</a>
  <a href="#" on:click|preventDefault={onClickNext}>next</a> -->
<svg class="sincontainer">
  
  {#each new Array(90).fill() as d, i}
    <circle 
      class="sindot"
      cx="{(x - i ) / 3.6 + 5}%"
      cy="{Math.cos((x - i*0.8) / 15) * 45+50}%"
      r="{(45- Math.abs(i-45))/9}" />
  {/each}

  <circle
    class="sindot maindot"
    cx="{x / 3.6}%"
    cy="{y * 45 + 50}%"
    r="5"
    />

</svg>
<Scrollmation 
  on:scroll={onScroll} 
  duration={4000}
  startPos={400}
  endPos={400}
  homePos={100}
  {scrollToPosition}
  {easing}
>
  <div slot="fg" />

</Scrollmation>
